<template>
    <div class="app-info">
        <span>{{info.name}}</span>
        <span>{{info.version}}</span>
        <span>{{info.description}}</span>
    </div>
</template>
<script>
module.exports = {
    name:"posaAbout",
    props: ['message'],
    data: function() {
        return {
            info: {
                name: ".mock",
                version: "1.0.0.mock",
                description: ".mock"
            }
        };
    },
    compiled: function() {},

}
</script>
<style scoped>
.app-info span {
    background-color: honeydew;
    display: block;
    width: auto;
    height: auto;
    font-size: medium;
    color: #00a1f1;
}
</style>



